import {
  ConfiguredConnectorTypes,
  ConnectorInterface,
} from '@xip-online-data/types';
import { ConnectorRuntimeInterface } from '@transai/connector-runtime';
import { ConnectorRunnerApiSink } from '@transai/connector-runner-api-sink';
import { ConnectorRunnerApiSource } from '@transai/connector-runner-api-source';
import { ConnectorRunnerDummySink } from '@transai/connector-runner-dummy-sink';
import { ConnectorRunnerDummySource } from '@transai/connector-runner-dummy-source';
import { ConnectorRunnerDummyNode } from '@transai/connector-runner-dummy-node';
import { ConnectorRunnerFileCopy } from '@transai/connector-runner-file-copy';
import {
  ConnectorRunnerFileSink,
  FileSinkConfig,
} from '@transai/connector-runner-file-sink';
import { ConnectorRunnerFileSource } from '@transai/connector-runner-file-source';
import { ConnectorRunnerSambaSink } from '@transai/connector-runner-samba-sink';
import { ConnectorRunnerSambaSource } from '@transai/connector-runner-samba-source';
import { ConnectorRunnerSqlSink } from '@transai/connector-runner-sql-sink';
import { ConnectorRunnerSqlSource } from '@transai/connector-runner-sql-source';
import { ConnectorRunnerCubeQuery } from '@transai/connector-runner-cube-query';
import { ConnectorRunnerMqtt } from '@transai/connector-runner-mqtt';
import { ConnectorRunnerFactorynebulaSource } from '@transai/connector-runner-factorynebula-source';

export const getConnectorType = (
  connectorConfig: ConnectorInterface,
): ConnectorRuntimeInterface => {
  switch (connectorConfig.connectorType) {
    case ConfiguredConnectorTypes.API_SINK:
      return new ConnectorRunnerApiSink(
        connectorConfig,
        connectorConfig.config as any,
        connectorConfig.actions,
      );
    case ConfiguredConnectorTypes.API_SOURCE:
      return new ConnectorRunnerApiSource(
        connectorConfig,
        connectorConfig.config as any,
        connectorConfig.actions,
      );
    case ConfiguredConnectorTypes.DUMMY_SINK:
      return new ConnectorRunnerDummySink(
        connectorConfig,
        connectorConfig.config as any,
        connectorConfig.actions,
      );
    case ConfiguredConnectorTypes.DUMMY_SOURCE:
      return new ConnectorRunnerDummySource(
        connectorConfig,
        connectorConfig.config as any,
        connectorConfig.actions,
      );
    case ConfiguredConnectorTypes.DUMMY_NODE:
      return new ConnectorRunnerDummyNode(
        connectorConfig,
        connectorConfig.config as any,
        connectorConfig.actions,
      );
    case ConfiguredConnectorTypes.FILE_COPY:
      return new ConnectorRunnerFileCopy(
        connectorConfig,
        connectorConfig.config as any,
        connectorConfig.actions,
      );
    case ConfiguredConnectorTypes.FILE_SINK:
      return new ConnectorRunnerFileSink(
        connectorConfig,
        connectorConfig.config as FileSinkConfig,
        connectorConfig.actions,
      );
    case ConfiguredConnectorTypes.FILE_SOURCE:
      return new ConnectorRunnerFileSource(
        connectorConfig,
        connectorConfig.config as any,
        connectorConfig.actions,
      );
    case ConfiguredConnectorTypes.SAMBA_SINK:
      return new ConnectorRunnerSambaSink(
        connectorConfig,
        connectorConfig.config as any,
        connectorConfig.actions,
      );
    case ConfiguredConnectorTypes.SAMBA_SOURCE:
      return new ConnectorRunnerSambaSource(
        connectorConfig,
        connectorConfig.config as any,
        connectorConfig.actions,
      );
    case ConfiguredConnectorTypes.SQL_SINK:
      return new ConnectorRunnerSqlSink(
        connectorConfig,
        connectorConfig.config as any,
        connectorConfig.actions,
      );
    case ConfiguredConnectorTypes.SQL_SOURCE:
      return new ConnectorRunnerSqlSource(
        connectorConfig,
        connectorConfig.config as any,
        connectorConfig.actions,
      );
    case ConfiguredConnectorTypes.CUBE_QUERY_RUNNER:
      return new ConnectorRunnerCubeQuery(
        connectorConfig,
        connectorConfig.config as any,
        connectorConfig.actions,
      );
    case ConfiguredConnectorTypes.MQTT:
      return new ConnectorRunnerMqtt(
        connectorConfig,
        connectorConfig.config as any,
        connectorConfig.actions,
      );
    case ConfiguredConnectorTypes.FACTORY_NEBULA_SOURCE:
      return new ConnectorRunnerFactorynebulaSource(
        connectorConfig,
        connectorConfig.config as any,
        connectorConfig.actions,
      );
    default:
      throw new Error(
        `Unknown connector type ${connectorConfig.connectorType}`,
      );
  }
};
